<template>
  <div>
    <a-card title="ozon商品管理">
      <a-row :gutter="16">
        <a-col :span="24" style="max-width: 200px; margin-bottom: 12px;">
          <a-input v-model="searchForm.keyWord" placeholder="关键字查询.." allowClear @pressEnter="search" />
        </a-col>
        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table size="small" :columns="columns" :dataSource="items" rowKey="id" :loading="loading" :pagination="pagination" @change="tableChange">
          <div slot="name" slot-scope="value">
            <a-tag :color="value ? 'green' : 'red'">{{value ? '激活' : '冻结'}}</a-tag>
          </div>
          <div slot="primary_image" slot-scope="text, record">
            <img :src="record.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
          </div>
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openFormModal(item)">查看</a-button>
              <a-button icon="edit" size="small" @click="openApprovalFormModal(item)" style="width: 60px; margin-left: 5px;">匹配</a-button>
            </a-button-group>
          </div>
        </a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :visible="visible" :form="targetItem" @update="update" />
    <match-form-modal v-model="approval_visible" :approval_visible="approval_visible" :form="targetItem" @update="update" />
  </div>
</template>

<script>
import {getOzonProductData } from '@/api/operation'

export default {
  name: 'OzonProduct',
  components: {
    FormModal: () => import('./FormModal.vue'),
    MatchFormModal:() => import('./MatchFormModal.vue')
  },
  data() {
    return {
      columns: [
        {
          title: '商品主图',
          dataIndex: 'primary_image',
          key: 'primary_image',
          scopedSlots: { customRender: 'primary_image' },
          width: '80px'
        },
        {
          title: '商品名称',
          dataIndex: 'name',
          width: '180px'
        },
        {
          title: 'offer_id',
          dataIndex: 'offer_id',
          width: 150
        },
        {
          title: 'product_id',
          dataIndex: 'product_id',
        },
        {
          title: 'sku',
          dataIndex: 'sku',
        },
        {
          title: '商品价格',
          dataIndex: 'price',
        },
        {
          title: 'fbo佣金百分比',
          dataIndex: 'fbo_commission_percent',
          customRender: (value, item, index) => {
            return value + "%"
          },
        },
        {
          title: 'fbo佣金额',
          dataIndex: 'fbo_commission_value',
        },
        {
          title: 'fbp佣金百分比',
          dataIndex: 'fbp_commission_percent',
          customRender: (value, item, index) => {
            return value + "%"
          },
        },
        {
          title: 'fbp佣金额',
          dataIndex: 'fbp_commission_value',
        },
        {
          title: 'fbs佣金百分比',
          dataIndex: 'fbs_commission_percent',
          customRender: (value, item, index) => {
            return value + "%"
          },
        },
        {
          title: 'fbs佣金额',
          dataIndex: 'fbs_commission_value',
        },
        {
          title: 'rfbs佣金百分比',
          dataIndex: 'rfbs_commission_percent',
          customRender: (value, item, index) => {
            return value + "%"
          },
        },
        {
          title: 'rfbs佣金额',
          dataIndex: 'rfbs_commission_value',
        },
        {
          title: '货币单位',
          dataIndex: 'currency_code',
        },
        {
          title: '店铺',
          dataIndex: 'shop',
          customRender: (value, item, index) => {
            return value?value.name:"未绑定"
          } 
        },
        {
          title: '负责人',
          dataIndex: 'owner',
          customRender: (value, item, index) => {
            return value?value.name:"未绑定"
          } 
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
          width: '156px'
        },
      ],
      searchForm: { start: 0, limit: 15, keyWord: ""},
      pagination: { current: 1, total: 0, pageSize: 15 },
      loading: false,
      items: [],

      visible: false,
      approval_visible:false,
      targetItem: {},
      form: {},
      importLoading: false,
    };
  },
  computed: {
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getOzonProductData(this.searchForm).then(data => {
        this.pagination.total = data.data.count;
        this.items = data.data.data;
      }).finally(() => {
        this.loading = false;
      });
    },
    update() {
      this.visible = false
      this.approval_visible = false
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    openFormModal(item) {
      this.targetItem = { ...item };
      this.visible = true;
    },
    openApprovalFormModal(item) {
      this.targetItem = { ...item };
      this.approval_visible = true;
    },
    tableChange(pagination, filters, sorter) {      
      this.searchForm.start = (pagination.current-1)*this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
  },
  mounted() {
    this.initialize();
  },
  activated() {
    this.initialize();
  },
}
</script>